const translateCity = (city: string, lan: 'zh' | 'en' = 'zh') => {
  let result = '';

  if (lan === 'zh') {
    switch (city) {
      case 'Taipei&NewTaipei':
        result = '  ';
        break;
      case 'Taipei':
        result = '';
        break;
      case 'NewTaipei':
        result = '';
        break;
      case 'Taoyuan':
        result = '';
        break;
      case 'Taichung':
        result = '';
        break;
      case 'Tainan':
        result = '';
        break;
      case 'Kaohsiung':
        result = '';
        break;
      case 'Other_City':
        result = '';
        break;
      default:
        result = city;
    }
  } else if (lan === 'en') {
    switch (city) {
      case '':
        result = 'Taipei';
        break;
      case '':
        result = 'NewTaipei';
        break;
      case '':
        result = 'Taoyuan';
        break;
      case '':
        result = 'Taichung';
        break;
      case '':
        result = 'Tainan';
        break;
      case '':
        result = 'Kaohsiung';
        break;
      default:
        result = city;
    }
  } else {
    throw new Error('translateCity Fn second parameter should be "zh" or "en"!');
  }

  return result;
};

export default translateCity;
